.App {
  text-align: center;
  padding: 5px
}

.header {
  padding: 20px;
  background-color: #3474E6;
  color: white;
  font-family: sans-serif;
  font-size: 24px;
}


.photo-wrapper {
  display: grid;
  grid-template-columns: 7fr 7fr 7fr 7fr 7fr 7fr 7fr;
  grid-template-rows: 6fr 6fr 6fr 6fr 6fr 6fr;
  margin-top: 50px;
  justify-content: center;
  height: calc(100vw - 250px);
  padding: 50px;
}

.image {
  height: 300px;
  width: 300px;
  object-fit: cover;
  margin: 5px;
}

.googleLogo {
  display: inline-block;
  background-image: url(data:image/svg+xml;base64,);
  width: 130px;
  height: 27px;
  background-repeat: no-repeat;
  background-position: center center;
  margin-left: 10px;
}

.google-logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.day {
  display: inline-block;
  background-color: #eee;
  border: 1px solid grey;
  padding: 0;
  position: relative;
  margin: 3px;
  background-position: center;
  background-size: cover;
}

.cal-date {
  position: absolute;
  right: 0px;
  width: 25px;
  height: 25px;
  font-size: 1em;
  background-color: rgba(255,255,255,.6);
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.empty {
  background-color: white;
}

.image-length {
  position: absolute;
  display: none;
  bottom: 0px;
  width: 25px;
  height: 25px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 789px) {
  .photo-wrapper {
    padding: 0;
    height: calc(100vw - 100px);
  }

  .image-length {
    display: none
  }

  .cal-date {
    font-size: 1em;
    background-color: rgba(200,200,200,.4);
  }
}